import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import './ServiceForm.css';
import { withTranslation } from 'react-i18next';

const GOOGLE_SHEETS_URL = 'https://script.google.com/macros/s/AKfycby-i16N4oD-MHF_OM_9DoZaQeCzqFxGZ2lLw1oTHREM_2n0v-Q/exec?';

class ServiceForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      message: '',
      result: null,
      submitEnabled: true
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.value;

    this.setState({ [name]: value });
  }

  handleSubmit() {
    const { t } = this.props;

    const toggleSubmitButton = function(enable) {
      this.setState({ submitEnabled: enable });
    }.bind(this);

    const resetResult = function(){
      setTimeout(() => this.setState({ result: null }), 2000);
    }.bind(this);

    const resetStateAndShowResult = function(result) {
      this.setState({ 
        name: '',
        email: '',
        message: '',
        result: result
      });
      resetResult();
      toggleSubmitButton(true);
    }.bind(this);

    toggleSubmitButton(false);

    fetch(GOOGLE_SHEETS_URL + new URLSearchParams(this.state), {
      method: 'GET',
      mode: 'no-cors'
    })
    .then(data => resetStateAndShowResult(t('form.result.ok')))
    .catch(error => resetStateAndShowResult(t('form.result.ko')));
  }

  render() {
    const { t } = this.props;

    return (
      <div className='form-container'>
        <div className='form-title label primary'>{ this.props.title }</div>
        <div className='service-form' >
          <FormControl fullWidth>
            <TextField name='name' required label={ t('form.name') } 
                       value={ this.state.name }
                       onChange={ this.handleInputChange } />
          </FormControl>
          <FormControl fullWidth>
            <TextField name='email' required label={ t('form.email') } 
                       value={ this.state.email }
                       onChange={ this.handleInputChange } />
          </FormControl>
          <FormControl fullWidth>
            <TextField name='message' required multiline rows={10} label={ t('form.hint') } 
                       value={ this.state.message }
                       onChange={ this.handleInputChange } />
          </FormControl>
          <div className='form-actions'>
            { this.state.result != null &&
            <p className='form-result'>{ this.state.result }</p>            
            }
            <Button size='large' variant='contained' color='secondary'
                    disabled={ !this.state.submitEnabled }
                    onClick={ this.handleSubmit }>
              { t('form.send') }
            </Button>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(ServiceForm);
